import { useEffect, useState } from 'react';
import { ReactTyped } from 'react-typed';
import avatarImg from '../assets/images/me-avatar.png';
import './Landing.css';
import Avatar8bit from './Avatar8bit';

function Landing() {
  const [showH2, setShowH2] = useState(false);
  const [showH1, setShowH1] = useState(false);
  const [glitchActive, setGlitchActive] = useState(false);
  const [showTaglinePrefix, setShowTaglinePrefix] = useState(false);
  const [startTypedWrapper, setStartTypedWrapper] = useState(false);

  useEffect(() => {
    //  PERFECT SEQUENCE TIMING
    
    // H2 appears after avatar lands
    const h2Timer = setTimeout(() => setShowH2(true), 2200);
    
    // H1 appears with glitch effect
    const h1Timer = setTimeout(() => {
      setShowH1(true);
      setGlitchActive(true);
    }, 3200);
    
    // Turn off glitch after 1.1s
    const glitchTimer = setTimeout(() => setGlitchActive(false), 4300);
    
    // "I'm" fades in
    const prefixTimer = setTimeout(() => setShowTaglinePrefix(true), 4500);
    
    // Typed strings start
    const typedTimer = setTimeout(() => setStartTypedWrapper(true), 5000);

    return () => {
      clearTimeout(h2Timer);
      clearTimeout(h1Timer);
      clearTimeout(glitchTimer);
      clearTimeout(prefixTimer);
      clearTimeout(typedTimer);
    };
  }, []);

  return (
    <header className="landing">
      <div className="hero-layout">
        <Avatar8bit />

        <div className="intro-block">
          {showH2 && (
            <h2 className="typed-heading fade-in">
              <ReactTyped
                strings={['Hello! I am']}
                typeSpeed={50}
                showCursor={false}
              />
            </h2>
          )}

          {/* H1 - With Glitch */}
          {showH1 && (
            <h1 className="typed-heading fade-in">
              <span className={glitchActive ? 'glitch-text' : ''}>
                <ReactTyped
                  strings={['ENGEL GATUS.']}
                  typeSpeed={60}
                  showCursor={false}
                />
              </span>
            </h1>
          )}

          <p className="tagline">
            {/* Tagline Prefix - Controlled Fade In */}
            <span className={`tagline-prefix ${showTaglinePrefix ? 'show' : ''}`}>
              I'm 
            </span>
            
            {/* Typed Wrapper - Starts Later */}
            <span className="typed-wrapper">
              {startTypedWrapper && (
                <ReactTyped
                  strings={[
                    'bridging the gap between what is and what could be.',
                    'creating systems that work for people, not the other way around.',
                    'automating the mundane to unlock the meaningful.',
                    'quietly solving problems that keep businesses running.',
                    'spotting inefficiencies before they become problems.',
                  ]}
                  typeSpeed={40}
                  startDelay={0}  // No delay
                  backSpeed={20}
                  backDelay={2000}
                  loop
                />
              )}
            </span>
          </p>
        </div>
      </div>
    </header>
  );
}

export default Landing;
